import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import {
  FormControl,
  FormGroupDirective,
  NgForm,
  Validators,
} from '@angular/forms';
import { NavigationEnd, Router } from '@angular/router';
import { faArrowLeft, faArrowRight } from '@fortawesome/free-solid-svg-icons';
import { LocalizeRouterService } from '@gilsdav/ngx-translate-router';
import { TranslateService } from '@ngx-translate/core';
import { filter } from 'rxjs';
import { emailPattern } from '../../../../constants';

@Component({
  selector: 'app-email-section',
  templateUrl: './email-section.component.html',
  styleUrls: ['./email-section.component.scss'],
})
export class EmailSectionComponent implements OnInit {
  emailFormControl = new FormControl('', [
    Validators.required,
    Validators.email,
    Validators.pattern(emailPattern),
  ]);
  notValidEmail = '';
  enterEmail = '';
  faArrow = faArrowRight;
  constructor(
    private translate: TranslateService,
    private router: Router,
    public localizeRouterService: LocalizeRouterService,
    private spy: ChangeDetectorRef,
  ) {}

  ngOnInit(): void {
    this.detectDir();
    this.router.events
      .pipe(filter((event) => event instanceof NavigationEnd))
      .subscribe(() => {
        this.detectDir();
      });
  }
  getErrorMessage() {
    this.enterEmail = this.translate.instant(
      'main.main-banner.email.requestEmail'
    );
    if (this.emailFormControl.hasError('required')) {
      return this.enterEmail;
    }
    this.notValidEmail = this.translate.instant(
      'main.main-banner.email.validEmail'
    );
    return this.emailFormControl.hasError('email') ? this.notValidEmail : '';
  }
  validateForm() {
    // Validate Form
    if (this.emailFormControl.invalid) {
      return false;
    }
    return true;
  }
  formSubmit() {
    if (this.validateForm()) {
      localStorage.setItem('email', this.emailFormControl.value!);
      this.router.navigate(['/contact-us']);
    }
  }
  
  detectDir() {
    this.faArrow =
      this.localizeRouterService.parser.currentLang == 'ar'
        ? faArrowLeft
        : faArrowRight;
    this.spy.detectChanges();
  }
}
