import {
  AfterViewInit,
  Component,
  OnInit,
  ViewChild,
} from '@angular/core';
import { SwiperOptions } from 'swiper';
import SwiperCore, { Autoplay } from 'swiper';
SwiperCore.use([Autoplay]);
import { SwiperComponent } from 'swiper/angular';
import { SCREEN_SM } from '../../../../constants';

@Component({
  selector: 'app-products',
  templateUrl: './products.component.html',
  styleUrls: ['./products.component.scss'],
})
export class ProductsComponent implements OnInit, AfterViewInit {
  swiperConfig: SwiperOptions = {
    a11y: { enabled: true },
    direction: 'horizontal',
    slidesPerView: 7,
    loop: true,
    autoplay: {
      delay: 1500,
      disableOnInteraction: false,
    },
    navigation: false,
    spaceBetween: 24,
    centeredSlides: true,
    centeredSlidesBounds: true,
    centerInsufficientSlides: true,
    breakpoints: {
      [SCREEN_SM]: {
        grabCursor: false,
      },
      0: {
        grabCursor: true,
      },
    },
  };
  @ViewChild('cont', { static: true }) cont: any;
  @ViewChild('swiperSlideShow', { static: true })
  swiperSlideShow!: SwiperComponent;

  constructor() {
   
  }
  ngAfterViewInit(): void {}
  ngOnInit(): void {
    const width = this.cont.nativeElement.offsetWidth;
    let slideCount = Math.floor(width / 178);
    this.swiperConfig.slidesPerView = slideCount == 1 ? 2 : slideCount;
  }
}
